.container {
    margin: 0 auto;
    padding: 0 20px;
    width: 100%;
    max-width: 375px;
}

.diary {
    display: flex;
    flex-direction: column;
    background-color: black;
    /* border: 1px solid black;*/
}

.calendarCont {
display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
        padding-top: 45px;
    /*margin-bottom: 24px;*/
    /*border: 1px solid black;*/
}



@media screen and (min-width: 768px) {
    .diary {
    position: relative;
    }
    .calendar {
    position: absolute;
    top: -10px;
        right: 0px;
    }
 .sectionCont {
    /*margin-top: 32px;*/
    
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        position: relative;
 }
  .container {
      padding: 0 32px;
      max-width: 100%;
  }
                  .calendarCont {
                      padding-top: 52px;
                  }

}

@media screen and (min-width: 1440px){
.sectionCont {
        /*margin-top: 32px;*/
