.statisticItem {
    padding: 14px;
    width: 157px;
    height: 96px;
    border-radius: 12px;
    border: 1px solid rgba(239, 237, 232, 0.2);
    background: rgba(239, 237, 232, 0.05);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;
    box-sizing: border-box;
}
.statisticItem:nth-child(2n) {
    width: 165px;
    height: 96px;
    /* Ширина другого стовпчика (парні елементи) */
}
.statisticItemPositive {
    border: 1px solid #3cbf61;
}

.statisticItemNegative {
    border: 1px solid #e9101d;
}

.primaryStatisticItem {
    border-radius: 12px;
    border: 1px solid var(--calendar-invalid-date);
    background: var(--decorate-color);
}

.statisticTitleWrap {
    display: flex;
    gap: 8px;
    align-items: center;
}

.statisticTitle {
    color: rgba(239, 237, 232, 0.4);
    width: 110px;
    font-size: 12px;
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    line-height: 1.33;
}

.statisticPrimaryTitle {
    color: rgba(239, 237, 232, 0.8);
}

.statisticIcon {
    width: 20px;
    height: 20px;
    fill: var(--accent-color);
}

.statisticValue {
    color: #efede8;
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    font-weight: 700;
    line-height: 1.11;
}

@media screen and (min-width: 768px) {
    .statisticItem {
        padding: 18px;
        width: 187px;
        height: 116px;
    }
        .statisticItem:nth-child(2n) {
        padding: 18px;
            width: 187px;
            height: 116px;
        }

    .statisticTitle {
        line-height: 1.5;
    }

    .statisticValue {
        font-family: 'Roboto', sans-serif;
        font-size: 24px;
        font-weight: 700;
        line-height: 1.33;
    }
}